import * as React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import Card from "../components/Card"
import Layout from "../components/layout"
import Seo from "../components/seo"
import { Form, Button, InputGroup, Breadcrumb, Table } from '@themesberg/react-bootstrap';
import Grid from '../components/Grid'
import { navigate } from "gatsby"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBell, faHome, faEnvelopeOpen, faSearch, faSignOutAlt, faUserShield, faAngleDown, faEye } from "@fortawesome/free-solid-svg-icons";
import Parse from '../vendor/Parse'
import { useLocation } from '@reach/router';
import NumberTable from '../components/NumberTable'
import { Modal } from '@themesberg/react-bootstrap';
import useAuth from "../hooks/userAuth";
const { useState } = React;

const Profile1 = "images/2021-01-30.jpg";
const IndexPage = () => {

  const location = useLocation();
  const qs = new URLSearchParams(location.search)
  const { isLogin } = useAuth()
  const [items, setItems] = useState([])
  const [query, setQuery] = useState(qs.get('search') || "")
  const [checklist, setChecklist] = useState([])
  const [displayInfo, setDisplayInfo] = useState(null)
  const searchNumbers = async (query) => {
    const res = await Parse.Cloud.run('queryNumbers', {
      query
    })
    setItems(res.data)
  }

  const notWhatsppFlow = ['admin', 'user'].includes(isLogin?.role)
  const getMySuggestion = async () => {
    const res = await Parse.Cloud.run('getMySuggestedNumber')
    setChecklist(res.checklist)
  }

  React.useEffect(() => {
    searchNumbers(query)
    getMySuggestion()
  }, [])

  const handleSearchButton = async () => {
    await searchNumbers(query)
  }

  const handleItem = (item) => {

    if (notWhatsppFlow) {
      setDisplayInfo(<>test</>)
    } else {
      const url = `https://api.whatsapp.com/send?phone=6587413159&text=` + encodeURIComponent(`Enquiry on ${item.num} with id ${item._id}`)
      navigate(url)
    }

  }

  const handleClose = () => {
    setDisplayInfo(null)
  }

  const showDefault = () => {

  }



  return <Layout afterSearch={<></>} >

    <h3 className="text-center">SG Mobile Number Finding</h3>
    <h2 className="text-center"><a style={{ color: '#25D366', textDecoration: 'none' }} href={`https://api.whatsapp.com/send?phone=6587413159&text=Find Mobile Number`}>whatsapp</a> us to access our number list</h2>
    <h3>Monitoring List</h3>
    <div>
      <h4>{checklist.join(', ')}</h4>
      We will notify you by email/whatsapp when your pattern is detected.
    </div>

    <h3>Free Search For Other Number</h3>
    <div className="d-xl-flex justify-content-between flex-wrap flex-md-nowrap align-items-center py-4 flex-column">
      <Form className="">
        <Form.Group id="topbarSearch">
          <InputGroup className="input-group-merge search-bar">
            <InputGroup.Text><FontAwesomeIcon icon={faSearch} /></InputGroup.Text>
            <Form.Control type="text" name="search" placeholder="Search" value={query} onChange={(e) => setQuery(e.target.value)} />
            <Button type="submit" onClick={handleSearchButton} variant="primary" size="sm" className="me-2">
              Find
            </Button>
          </InputGroup>
        </Form.Group>
      </Form>
      <NumberTable items={items} onItem={item => handleItem && handleItem(item)} />


    </div>

    <Modal as={Modal.Dialog} centered show={displayInfo} onHide={handleClose}>
      <Modal.Header>
        <Modal.Title className="h6">Information</Modal.Title>
        <Button variant="close" aria-label="Close" onClick={handleClose} />
      </Modal.Header>
      <Modal.Body>
        {displayInfo}
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>
          I Got It
        </Button>
        <Button variant="link" className="text-gray ms-auto" onClick={handleClose}>
          Close
        </Button>
      </Modal.Footer>
    </Modal>
  </Layout >
}

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <Seo title="Home" />

export default IndexPage
